"use client";
import { prisma } from "@/lib/prisma";
import { LineChart, Line, BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend, Cell } from 'recharts';
import { TrendingUp } from 'lucide-react';

interface ChartDataPoint {
    date: string;
    value: number;
    quantity: number;
    tonnage: number;
}

interface CategoryDataPoint {
    category: string;
    quantity: number;
}

interface Props {
    salesTrendData: ChartDataPoint[];
    sales: any[]; // Add sales prop
}

const productList = await prisma.productList.findMany({
    select: { id: true, weightValue: true, packSize: true },
});

export default function SalesTrendChart({ salesTrendData, sales }: Props) {
    if (!salesTrendData.length || !sales || sales.length === 0) {
        return <p>No sales data available.</p>;
    }

    // Transform data for category bar chart (year-to-date)

    // Fetch productList once


    const categories = Array.from(
        new Set(
            sales
                .filter(sale => new Date(sale.saleDate).getFullYear() === new Date().getFullYear())
                .map(sale => sale.product?.category || 'Uncategorized')
        )
    );

    const categoryData = categories.map(category => {
        const categorySales = sales
            .filter(sale =>
                new Date(sale.saleDate).getFullYear() === new Date().getFullYear() &&
                sale.product?.category === category
            );
        return {
            category,
            tonnage: categorySales.reduce((sum, sale) => {
                const product = productList.find(p => p.id === sale.productId);
                const weightInTons = product ? (product.weightValue * sale.quantity * product.packSize) / 1000 : 0;
                return sum + weightInTons;
            }, 0),
            quantity: categorySales.reduce((sum, sale) => sum + sale.quantity, 0),
            salesValue: categorySales.reduce((sum, sale) => sum + sale.quantity * sale.salePrice, 0),
        };
    });


    return (
        <div className="space-y-6">
            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                {/* Sales Value Trend */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <TrendingUp className="h-5 w-5 text-green-500" /> Sales Value Trend
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Sales Value (K)</h4>
                    <ResponsiveContainer width="100%" height={200}>
                        <LineChart data={salesTrendData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="date" />
                            <YAxis />
                            <Tooltip formatter={(value) => [`${value}`, 'Sales Value']} />
                            <Line type="monotone" dataKey="value" stroke="#10b981" strokeWidth={2} />
                        </LineChart>
                    </ResponsiveContainer>
                </div>

                {/* Sales Tonnage Trend */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <TrendingUp className="h-5 w-5 text-green-500" /> Sales Tonnage Trend
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Tonnage</h4>
                    <ResponsiveContainer width="100%" height={200}>
                        <LineChart data={salesTrendData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="date" />
                            <YAxis />
                            <Tooltip formatter={(value) => [`${value} tons`, 'Tonnage']} />
                            <Line type="monotone" dataKey="tonnage" stroke="#f97316" strokeWidth={2} />
                        </LineChart>
                    </ResponsiveContainer>
                </div>

                {/* Sales Quantity Trend */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <TrendingUp className="h-5 w-5 text-green-500" /> Sales Quantity Trend
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Sales Quantity</h4>
                    <ResponsiveContainer width="100%" height={200}>
                        <LineChart data={salesTrendData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="date" />
                            <YAxis />
                            <Tooltip formatter={(value) => [`${value}`, 'Quantity']} />
                            <Line type="monotone" dataKey="quantity" stroke="#3b82f6" strokeWidth={2} />
                        </LineChart>
                    </ResponsiveContainer>
                </div>

                {/* Sales Category Trend */}
                <div className="bg-white p-6 rounded-xl border hover:shadow-md transition-shadow">
                    <h3 className="font-semibold mb-4 flex items-center gap-2">
                        <TrendingUp className="h-5 w-5 text-green-500" /> Sales Category Trend (Year-to-Date)
                    </h3>
                    <h4 className="text-sm font-medium text-gray-500 mb-2">Tonnage by Category</h4>
                    <ResponsiveContainer width="100%" height={200}>
                        <BarChart data={categoryData}>
                            <CartesianGrid strokeDasharray="3 3" />
                            <XAxis dataKey="category" />
                            <YAxis />
                            <Tooltip
                                formatter={(value, name, props) => {
                                    const { quantity, salesValue } = props.payload;
                                    return [
                                        `${value} tons`,
                                        'Tonnage',
                                        `Quantity: ${quantity} units`,
                                        `Sales Value: K${salesValue.toFixed(2)}`
                                    ];
                                }}
                            />
                            <Legend />
                            <Bar dataKey="tonnage" name="Tonnage">
                                {categoryData.map((entry, index) => (
                                    <Cell
                                        key={`cell-${index}`}
                                        fill={['#3b82f6', '#10b981', '#f97316', '#8b5cf6', '#ec4899'][index % 5]}
                                    />
                                ))}
                            </Bar>
                        </BarChart>
                    </ResponsiveContainer>
                </div>
            </div>
        </div>
    );
}
